<template>
  <div class="hidden md:block">
    <!-- 일반 메뉴 -->
    <div class="flex justify-between">
      <!-- 좌측 정렬-->
      <div class="flex items-center">
        <router-link class="pl-3" to="/">
          <img
            class="w-8 h-8 rounded-full"
            alt="logo"
            src="/images/logo-sm.png"
          />
        </router-link>

        <div class="px-3 font-bold">
          <router-link to="/">
            <small>
              <div class="">메타버스로</div>
              <div>가즈아~</div>
            </small>
          </router-link>
        </div>

        <div class="px-3">
          <router-link to="/about">소개</router-link>
        </div>

        <div class="px-3">
          <router-link to="/tile">타일</router-link>
        </div>

        <div class="px-3">
          <router-link to="/wall">벽</router-link>
        </div>

        <div class="px-6">
          <router-link to="/invest">투자</router-link>
        </div>
      </div>
      <!-- 우측 정렬-->
      <div class="flex items-center" v-if="!waitResponse">
        <div class="mr-3">
          <router-link
            v-if="!$store.getters.isLogin"
            to="/login"
            class="tw-indigo-btn"
            >로그인</router-link
          >
          <router-link v-else to="/profile" class="tw-indigo-btn">{{
            $store.getters.userName
          }}</router-link>
        </div>
        <div>
          <router-link
            v-if="!$store.getters.isLogin"
            to="/signup"
            class="tw-yello-btn"
            >가입</router-link
          >
        </div>
      </div>
    </div>
  </div>
  <!-- sm 메뉴 -->
  <div class="block md:hidden">
    <div class="w-full flex justify-between">
      <div class="flex items-center">
        <router-link class="" to="/">
          <img
            class="w-8 h-8 rounded-full"
            alt="logo"
            src="/images/logo-sm.png"
          />
        </router-link>
        <div class="w-2"></div>
        <router-link to="/">
          <small class="font-bold">메타버스로 가즈아~</small>
        </router-link>
      </div>
      <button @click="onMobileMenu()">
        <img class="w-8 h-8" src="/images/hero-menu.svg" />
      </button>
    </div>
    <div :class="mobileMenuClass">
      <div class="pt-4 flex justify-around">
        <div>
          <router-link to="/about">소개</router-link>
        </div>
        <div class="">
          <router-link to="/tile">타일</router-link>
        </div>
        <div class="">
          <router-link to="/wall">벽</router-link>
        </div>
        <div class="">
          <router-link to="/invest">투자</router-link>
        </div>
      </div>
      <div class="flex pt-2 justify-around">
        <router-link
          v-if="!$store.getters.isLogin"
          to="/login"
          class="tw-indigo-btn w-32"
          >로그인</router-link
        >
        <router-link
          v-if="!$store.getters.isLogin"
          to="/signup"
          class="tw-yello-btn w-32"
          >가입</router-link
        >
      </div>
      <div class="flex pt-2">
        <router-link
          v-if="$store.getters.isLogin"
          to="/profile"
          class="tw-indigo-btn w-32"
          >{{ $store.getters.userName }}</router-link
        >
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      waitResponse: false,
      isShowMobileMenu: false,
    };
  },
  async mounted() {
    // console.log("Header mounted");

    const loginToken = localStorage.getItem("loginToken");
    if (loginToken === null) {
      return;
    }

    const reqData = {
      api: "loginWithToken",
      loginToken: loginToken,
    };
    // console.log("Header token exists", reqData);

    this.$store.state.waitingLogin = true;
    this.waitResponse = true;
    const res = await this.$store.callAsync(reqData);
    this.waitResponse = false;
    this.$store.state.waitingLogin = false;

    if (res.code != 100) {
      // console.log("Header loginWithToken ng", res.message);
      if (res.code == -2) {
        localStorage.setItem("loginToken", null);
        return;
      }
      return;
    }

    // console.log("Header loginWithToken ok", res);
    this.$store.commit("setUser", res.user);
  },
  computed: {
    mobileMenuClass() {
      return this.isShowMobileMenu ? "" : "hidden";
    },
  },
  methods: {
    onMobileMenu() {
      this.isShowMobileMenu = !this.isShowMobileMenu;
    },
  },
};
</script>
