<template>
  <div>
    <div class="pb-2 grid lg:grid-cols-3 md:grid-cols-2 grid-cols-1 gap-2">
      <!-- card -->
      <div class="tw-home-card">
        <img class="tw-home-card-img" src="images/pic_logo.png" />
        <div class="tw-home-card-div">
          <div class="p-2">타일을 만들어봅니다.</div>
          <button @click="onPic()" class="tw-indigo-btn">GO</button>
        </div>
      </div>

      <div class="tw-home-card">
        <img class="tw-home-card-img" src="images/wall_logo.png" />
        <div class="tw-home-card-div">
          <div class="p-2">타일을 붙일 수 있는 벽을 만듭니다.</div>
          <button @click="onWall()" class="tw-indigo-btn">GO</button>
        </div>
      </div>

      <div class="tw-home-card">
        <img class="tw-home-card-img" src="images/invest_logo.png" />
        <div class="tw-home-card-div">
          <div class="p-2">메타버스 관련주를 모아봅니다.</div>
          <button @click="onInvest()" class="tw-indigo-btn">GO</button>
        </div>
      </div>
    </div>
    <!-- card end -->
    <div class="grid md:grid-cols-2 grid-cols-1 gap-2">
      <div class="bg-white tw-rounded p-1">
        <div class="p-1 text-sm font-bold">메타버스 소식</div>
        <div v-for="data in news" :key="data.rid">
          <div class="px-4">
            <div class="rounded-full w-full bg-blue-200 h-1"></div>
          </div>
          <div
            class="text-left break-all"
            v-html="$store.markdown(data.contents)"
          ></div>
          <div class="text-left text-xs">
            {{ $store.getAgo(data.last_create) }}
          </div>
        </div>
      </div>
      <div class="bg-white tw-rounded p-1">
        <div class="p-1 text-sm font-bold">업데이트 소식</div>
        <div v-for="data in updates" :key="data.rid">
          <div class="px-4">
            <div class="rounded-full w-full bg-red-200 h-1"></div>
          </div>
          <div
            class="text-left break-all"
            v-html="$store.markdown(data.contents)"
          ></div>
          <div class="text-left text-xs">
            {{ $store.getAgo(data.last_create) }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loadingData: false,
      errorMessage: "",
      news: [],
      updates: [],
    };
  },
  async mounted() {
    const reqData = {
      api: "getHome",
      loginToken: localStorage.getItem("loginToken"),
      userUid: this.$store.getters.userUid,
    };

    // console.log("getHome req", reqData);
    this.loadingData = true;
    const res = await this.$store.callAsync(reqData);
    this.loadingData = false;

    // console.log("getHome res", res);
    if (res.code != 100) {
      this.errorMessage = res.message;
      return;
    }

    const resData = res.home;
    for (let i = 0; i < resData.news.length; i++) {
      const n = resData.news[i];
      // console.log("m", this.$store.getMoment(n.dt_create));
      this.news.push({
        contents: n.contents,
        last_create: this.$store.getMoment(n.dt_create),
      });
    }
    for (let i = 0; i < resData.updates.length; i++) {
      const n = resData.updates[i];
      // console.log("m", this.$store.getMoment(n.dt_create));
      this.updates.push({
        contents: n.contents,
        last_create: this.$store.getMoment(n.dt_create),
      });
    }
  },
  methods: {
    onPic() {
      this.$router.push("/tile");
    },
    onWall() {
      this.$router.push("/wall");
    },
    onInvest() {
      this.$router.push("/invest");
    },
  },
};
</script>
